import { Container } from "react-bootstrap";
import Table from "react-bootstrap/Table";
import { dataSources } from "./dataSources";

import Badge from "react-bootstrap/Badge";
import ListGroup from "react-bootstrap/ListGroup";

const HousingDescriptions = () => {
  const localPdf = (ym, source) => {
    if (source === "-" || source === "") {
      return "-";
    } else {
      const em = ym.slice(0, 4) - 1911 + ym.slice(4, 6);
      return (
        <a
          href={
            "https://github.com/iCareTw/housing/blob/main/data/pdf_SocialHousing/" +
            em +
            ".pdf"
          }
          target="_blank"
          rel="noreferrer"
        >
          {em}.pdf
        </a>
      );
    }
  };

  const sourcePdf = (ym, originUrl) => {
    if (originUrl === "-" || originUrl === "") {
      return "-";
    } else {
      return (
        <a href={`${originUrl}`} target="_blank" rel="noreferrer">
          {originUrl.split("/").pop()}
        </a>
      );
    }
  };

  return (
    <Container>
      <h2>
        <Badge bg="primary"></Badge>
      </h2>
      <ListGroup>
        <ListGroup.Item>
           <code>pip.moi.gov.tw</code> - 
        </ListGroup.Item>
        <ListGroup.Item>201809(10709), </ListGroup.Item>
        <ListGroup.Item>
          202003(10903),  <code></code>
        </ListGroup.Item>
        <ListGroup.Item>202004(10904),  /</ListGroup.Item>
        <ListGroup.Item>
          202008(10908), Missing Data.  166  128 , 
        </ListGroup.Item>
        <ListGroup.Item>
          202012(10912),  <code></code>
        </ListGroup.Item>
        <ListGroup.Item>
          202303(11203), (),  <code></code> {" "}
          <code></code>
        </ListGroup.Item>
        <ListGroup.Item>202411(11311), </ListGroup.Item>
        <ListGroup.Item>
          202412(11312),  <code></code>  <code></code> (pdf {" "}
          <code></code>)
        </ListGroup.Item>
      </ListGroup>

      <h2>
        <Badge bg="info"></Badge>
      </h2>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {dataSources.map(item =>
            <tr key={item.ym}>
              <td>
                {item.ym}
              </td>
              <td>
                {localPdf(item.ym, item.source)}
              </td>
              <td>
                {sourcePdf(item.ym, item.source)}
              </td>
            </tr>
          )}
        </tbody>
      </Table>
    </Container>
  );
};

export default HousingDescriptions;
